import Vue from 'vue'

import Vuex from 'vuex'
Vue.use(Vuex)
import Vonic from 'vonic'
// Page Components
import Index from './components/Index.vue'
import About from './components/About.vue'
import store from './vuex'
// Routes
const routes = [{
	path: '/',
	component: Index
}, {
	path: '/about',
	component: About
}]

Vue.use(Vonic.app, {
	routes: routes,
	store: store
})


